// log
import store from "../store";

const fetchTokenDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchTokenDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchTokenDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

export const fetchTokenData = (_owner, _spender) => {
  return async (dispatch) => {
    dispatch(fetchTokenDataRequest());
    try {
      console.log(_owner, _spender);
      let tokensApproved = await store
        .getState()
        .blockchain.tokenSmartContract.methods.allowance(_owner, _spender)
        .call();
      let tokenBalance = await store
        .getState()
        .blockchain.tokenSmartContract.methods.balanceOf(_owner)
        .call();
      let mintedWithBch = await store
        .getState()
        .blockchain.nftSmartContract.methods.mintedWithBch()
        .call();
      let mintedWithNftc = await store
        .getState()
        .blockchain.nftSmartContract.methods.mintedWithNftc()
        .call();
      let totalSupply = Number(mintedWithBch) + Number(mintedWithNftc);


      dispatch(
        fetchTokenDataSuccess({
          tokensApproved,
          tokenBalance,
          mintedWithBch,
          mintedWithNftc,
          totalSupply,
        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchTokenDataFailed("Could not load data from contract."));
    }
  };
};
