const initialState = {
  loading: false,
  mintedWithNftc: 0,
  mintedWithBch: 0,
  totalSupply: 0,
  cost: 0,
  error: false,
  errorMsg: "",
};

const nftDataReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CHECK_DATA_REQUEST":
      return {
        ...state,
        loading: true,
        error: false,
        errorMsg: "",
      };
    case "CHECK_DATA_SUCCESS":
      return {
        ...state,
        loading: false,
        mintedWithNftc: action.payload.mintedWithNftc,
        mintedWithBch: action.payload.mintedWithBch,
        totalSupply: action.payload.totalSupply,
        // cost: action.payload.cost,
        error: false,
        errorMsg: "",
      };
    case "CHECK_DATA_FAILED":
      return {
        ...initialState,
        loading: false,
        error: true,
        errorMsg: action.payload,
      };
    default:
      return state;
  }
};

export default nftDataReducer;
