// constants
import Web3EthContract from "web3-eth-contract";
import Web3 from "web3";
// log
import { fetchNftData } from "../nftData/nftDataActions";
import { fetchTokenData } from "../tokenData/tokenDataActions";

const connectRequest = () => {
  return {
    type: "CONNECTION_REQUEST",
  };
};

const connectSuccess = (payload) => {
  return {
    type: "CONNECTION_SUCCESS",
    payload: payload,
  };
};

const connectFailed = (payload) => {
  return {
    type: "CONNECTION_FAILED",
    payload: payload,
  };
};

const updateAccountRequest = (payload) => {
  return {
    type: "UPDATE_ACCOUNT",
    payload: payload,
  };
};

export const connect = () => {
  return async (dispatch) => {
    dispatch(connectRequest());
    
    const nftAbiResponse = await fetch("/config/nft_abi.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const nftAbi = await nftAbiResponse.json();

    const tokenAbiResponse = await fetch("/config/token_abi.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const tokenAbi = await tokenAbiResponse.json();


    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const CONFIG = await configResponse.json();
    const { ethereum } = window;
    const metamaskIsInstalled = ethereum && ethereum.isMetaMask;
    if (metamaskIsInstalled) {
      Web3EthContract.setProvider(ethereum);
      let web3 = new Web3(ethereum);
      try {
        const accounts = await ethereum.request({
          method: "eth_requestAccounts",
        });
        const networkId = await ethereum.request({
          method: "net_version",
        });
        if (networkId == CONFIG.NETWORK.ID) {
          const nftSmartContractObj = new Web3EthContract(
            nftAbi,
            CONFIG.NFT_CONTRACT_ADDRESS
          );
          const tokenSmartContractObj = new Web3EthContract(
            tokenAbi,
            CONFIG.TOKEN_CONTRACT_ADDRESS
          );
          dispatch(
            connectSuccess({
              account: accounts[0],
              nftSmartContract: nftSmartContractObj,
              tokenSmartContract: tokenSmartContractObj,
              web3: web3,
            })
          );
          // Add listeners start
          ethereum.on("accountsChanged", (accounts) => {
            dispatch(updateAccount(accounts[0],"0xb72df4BD9951dDD59bbE2C5FC4d43d17d5B429df"));
          });
          ethereum.on("chainChanged", () => {
            window.location.reload();
          });
          // Add listeners end
        } else {
          dispatch(connectFailed(`Change network to ${CONFIG.NETWORK.NAME} and then reload this website.`));
        }
      } catch (err) {
        dispatch(connectFailed("Something went wrong: " + err));
      }
    } else {
      dispatch(connectFailed("Install Metamask."));
    }
  };
};

export const updateAccount = (account, nftCa) => {
  return async (dispatch) => {
    dispatch(updateAccountRequest({ account: account }));
    dispatch(fetchNftData(account));
    dispatch(fetchTokenData(account, nftCa));
  };
};
