// log
import store from "../store";

const fetchNftDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchNftDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchNftDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

export const fetchNftData = () => {
  return async (dispatch) => {
    dispatch(fetchNftDataRequest());
    try {
      let mintedWithBch = await store
        .getState()
        .blockchain.nftSmartContract.methods.mintedWithBch()
        .call();
      let mintedWithNftc = await store
        .getState()
        .blockchain.nftSmartContract.methods.mintedWithNftc()
        .call();
      let totalSupply = Number(mintedWithBch) + Number(mintedWithNftc);
      // let cost = await store
      //   .getState()
      //   .blockchain.nftSmartContract.methods.cost()
      //   .call();

      dispatch(
        fetchNftDataSuccess({
          mintedWithBch,
          mintedWithNftc,
          totalSupply,
          // cost,
        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchNftDataFailed("Could not load data from contract."));
    }
  };
};
